import { POST, PUT } from '../../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["form", "onClose", "loadData", "textTitle", "disableView", "disableEdit"],
  data() {
    return {
      isLoading: true
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    onSave: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.form.Id,
          ProductCode: this.form.ProductCode,
          OrderTypeCode: this.form.OrderTypeCode,
          ProductName: this.form.ProductName,
          Price: this.form.Price,
          Description: this.form.Description,
          Bobot: this.form.Bobot,
        }
        if (dataReq.Id === null) {
          POST("productcatalog", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.onClose();
          })
        } else {
          PUT("productcatalog", dataReq).then(() => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.onClose();
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
  }
};
