import formProductCatalog from "./formProductCatalog";
import { POST, DELETE } from '../../api/index'
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox"

export default {
  name: "client",
  components: { formProductCatalog, HalfCircleSpinner },
  data() {
    return {
      isLoading: false,
      isShow: false,
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      disableView: false,
      disableEdit: false,
      textTitle: "",
      dataProductCatalog: [],
      form: {
        Id: null,
        ProductCode: null,
        OrderTypeCode: null,
        ProductName: null,
        Price: null,
        Description: null,
        Bobot: null,
      },
      headers: [{
        text: "Aksi",
        value: "aksi",
        sortable: false,
        align: "left",
        class: ""
      }, {
        text: "Product Code",
        value: "ProductCode",
        align: "center"
      }, {
        text: "Order Type Code",
        value: "OrderTypeCode",
        align: "center"
      }, {
        text: "Product Name",
        value: "ProductName",
        align: "center"
      }, {
        text: "Price",
        value: "Price",
        align: "center"
      }, {
        text: "Description",
        value: "Description",
        align: "center"
      }, {
        text: "Bobot",
        value: "Bobot",
        align: "center"
      }]
    };
  },
  created() {
    this.loadData()
  },
  beforeMount: function () { },
  computed: {},
  methods: {
    toggleInputDialog: function () {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Product Catalog"
    },
    onClose: function () {
      this.isShow = false;
      this.disableEdit = false;
      this.disableView = false;
      this.resetForm()
    },
    loadData: function () {
      try {
        this.isLoading = true
        const { page, rowsPerPage } = this.pagination;
        POST(`productcatalog/productcatalog_cr/${page}/${rowsPerPage}`).then((res) => {
          this.dataProductCatalog = res.Data.map(x => {
            return ({
              Id: x.Id,
              ProductCode: x.ProductCode,
              OrderTypeCode: x.OrderTypeCode,
              ProductName: x.ProductName,
              Price: x.Price,
              Description: x.Description,
              Bobot: x.Bobot,
            })
          })
          this.totalData = res.TotalData;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onEdit: function (id) {
      try {
        this.isLoading = true
        POST('productcatalog/productcatalog_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = false;
          this.disableEdit = true;
          this.textTitle = "Ubah Product Catalog"
          this.isShow = true;
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    onDelete: function (id) {
      deleteData(id, this.delData)
    },
    delData: function (id) {
      try {
        this.isLoading = true
        DELETE(`productcatalog/${id}`).then(() => {
          success("Data Berhasil Dihapus")
          this.loadData()
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onView: function (id) {
      try {
        this.isLoading = true;
        POST('productcatalog/productcatalog_cr', { Id: id }).then((res) => {
          this.form = res[0]
          this.disableView = true;
          this.isShow = true;
          this.textTitle = "Lihat Product Catalog"
          this.isLoading = false
        })
      } catch (err) {
        this.isLoading = false
      }
    },
    resetForm: function () {
      this.form = {
        Id: null,
        ProductCode: null,
        OrderTypeCode: null,
        ProductName: null,
        Price: null,
        Description: null,
        Bobot: null,
      }
    }
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`productcatalog/productcatalog_cr/${page}/${rowsPerPage}`).then(res => {
            this.dataProductCatalog = res.Data.map(x => {
              return {
                Id: x.Id,
                ProductCode: x.ProductCode,
                OrderTypeCode: x.OrderTypeCode,
                ProductName: x.ProductName,
                Price: x.Price,
                Description: x.Description,
                Bobot: x.Bobot,
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  }
};
